import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup} from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-unidadmateria-create',
  templateUrl: 'unidadmateria-create.html',
})
export class UnidadmateriaCreatePage {

  public form                  : FormGroup;
  username: string = "";
  
  public idtipo                 : any;
  public idtipo2                 : any;
  public idnombre                 : any;
  

  public lenguaje                 : any;
  public matematica	     : any;
  public fisica		     : any;
  public biologia	     : any;
  public quimica		     : any;

  idempresa:any;
  private _ID                  : String;

  public pageTitle           	: string;
  createdCode = null;
  scannedCode = null;

  isDisablednit :any;
  constructor(  public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
     this.form = this._FB.group({
       lenguaje : [''],
       biologia : [''],
       matematica : [''],
       fisica : [''],
       quimica : ['']
      
     });

   

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {this.idtipo=this.navParams.data.record2.idtipo;
    this.idtipo2=this.navParams.data.record2.idtipo2;
    this.idnombre=this.navParams.data.record2.idnombre;
    
    if(this.navParams.get("record")==null)
    {
      this.limpia('false');   
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
      this.limpia('false');
       this._ID 				=	this.navParams.data.record[0]._id;
       this.lenguaje		=	this.navParams.data.record[0].lenguaje;
       this.matematica		=	this.navParams.data.record[0].matematica;
       this.fisica		=	this.navParams.data.record[0].fisica;
       this.biologia		=	this.navParams.data.record[0].biologia;
       this.quimica		=	this.navParams.data.record[0].quimica;
      
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  limpia(op:string) : void
  {
   
    this.form.controls['lenguaje'].setValue(op);
     this.form.controls['matematica'].setValue(op);
     this.form.controls['fisica'].setValue(op);
     this.form.controls['biologia'].setValue(op);
    this.form.controls['quimica'].setValue(op);
  }
    

 
  

  manageGallery() : void
  {
    
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
   


     let   options       : any	     = { 
      idtipounidad  : this.idtipo,
      idunidadacademica : this.idtipo2,
   
     lenguaje : this.form.controls['lenguaje'].value,
     matematica : this.form.controls['matematica'].value,
     fisica : this.form.controls['fisica'].value,
     biologia : this.form.controls['biologia'].value,
     quimica: this.form.controls['quimica'].value,
 
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' facultad materias '}
    };


     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'facultadmaterias').then((result) => {
                 if (!this.form.valid) { return; }
                 this.viewCtrl.dismiss(result);
         
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'facultadmaterias').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }
  stringify(country){
    return JSON.stringify(country);
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }
  optionsFn(item) {//here item is an object 
    console.log(item);
  }

}



